.selection > button {
  padding: 10px 16px;
  background-color: #171717;
  color: #f4f4f4;
  border-radius: 10px;
  font-size: 1.2rem;
  margin-bottom: 10px;
}
.selection {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 2rem 0;
}
.selection > img {
  max-width: 100px;
  margin: 0 10px;
  margin-bottom: 16px;
}
